const box = document.querySelector('.DnD__draggable');
const DnD = document.querySelector('.DnD');
const DnD__origin = document.querySelector('.DnD__origin');
const dropBacklight = 'pink';
const dropzoneColor = '#a6e0be';

function randomColor() {
  return '#' + Math.floor(Math.random() * 16777215).toString(16);
}

box.addEventListener('pointerdown', (e) => pointerDownDnD(e));

function pointerDownDnD(event) {
  // shift - Ñ‡Ñ‚Ð¾Ð±Ñ‹ Ð¿Ñ€Ð¸ drag ÐºÑƒÑ€ÑÐ¾Ñ€ Ð½Ð°Ñ…Ð¾Ð´Ð¸Ð»ÑÑ Ð½Ð°Ð´ Ð¼ÐµÑÑ‚Ð¾Ð¼ ÐºÐ»Ð¸ÐºÐ° Ð½Ð° ÐºÐ²Ð°Ð´Ñ€Ð°Ñ‚Ðµ.
  let shiftX = event.clientX - event.target.getBoundingClientRect().left;
  let shiftY = event.clientY - event.target.getBoundingClientRect().top;

  // ÑÐ¾Ð·Ð´Ð°ÐµÐ¼ Ð½Ð¾Ð²Ñ‹Ð¹ Ð±Ð¾ÐºÑ ÐµÑÐ»Ð¸ Ð¿Ñ€Ð¾ÑˆÐ»Ñ‹Ð¹ Ð±Ñ‹Ð» Ð² Ð¾Ñ€Ð¸Ð´Ð¶Ð¸Ð½Ðµ (Ñ€Ð°Ð½Ð´Ð¾Ð¼Ð¸Ð¼ Ñ†Ð²ÐµÑ‚ Ð¸ Ð²ÐµÑˆÐ°ÐµÐ¼ ÑÑ‚Ð¾Ñ‚ Ð¶Ðµ Ð¾Ð±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸Ðº Ð´ÐµÐ¹ÑÑ‚Ð²Ð¸Ð¹):
  if (event.target.parentElement == DnD__origin) {
    let newBox = event.target.cloneNode();
    newBox.addEventListener('pointerdown', (e) => pointerDownDnD(e));
    newBox.style.background = randomColor();
    DnD__origin.appendChild(newBox);
  }

  event.target.style.position = 'absolute';

  //ÐºÐ»Ð°Ð´ÐµÐ¼ Ð¿ÐµÑ€ÐµÑ‚Ð°ÑÐºÐ¸Ð²Ð°ÐµÐ¼Ñ‹Ð¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð½Ð° Ð²ÐµÑ€Ñ…Ð½Ð¸Ð¹ ÑƒÑ€Ð¾Ð²ÐµÐ½ÑŒ DOM
  DnD.append(event.target);

  moveAt(event.pageX, event.pageY);

  function moveAt(pageX, pageY) {
    event.target.style.left = pageX - shiftX + 'px';
    event.target.style.top = pageY - shiftY + 'px';
  }

  let currentDroppable = null;

  function onPointerMove(event) {
    //Ð¿Ñ€Ð¸ Ð´Ð²Ð¸Ð¶ÐµÐ½Ð¸Ð¸ ÐºÑƒÑ€ÑÐ¾Ñ€Ð¾Ð¼ - Ð¿ÐµÑ€ÐµÐ´Ð²Ð¸Ð³Ð°ÐµÐ¼ ÐºÐ²Ð°Ð´Ñ€Ð°Ñ‚ Ð·Ð° ÐºÑƒÑ€ÑÐ¾Ñ€Ð¾Ð¼.
    moveAt(event.pageX, event.pageY);

    //Ð¿Ð¾Ð»ÑƒÑ‡Ð°ÐµÐ¼ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð¿Ð¾Ð´ Ð¿ÐµÑ€ÐµÐ½Ð¾ÑÐ¸Ð¼Ñ‹Ð¼ ÐºÐ²Ð°Ð´Ñ€Ð°Ñ‚Ð¾Ð¼
    event.target.hidden = true;
    let elemBelow = document.elementFromPoint(event.clientX, event.clientY);
    event.target.hidden = false;

    //Ð¿Ð¾Ð»ÑƒÑ‡Ð°ÐµÐ¼ Ð±Ð»Ð¸Ð¶Ð°Ð¹ÑˆÐµÐ³Ð¾ Ñ€Ð¾Ð´Ð¸Ñ‚ÐµÐ»Ñ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° Ð¿Ð¾Ð´ Ð¿ÐµÑ€ÐµÐ½Ð¾ÑÐ¸Ð¼Ñ‹Ð¼ ÐºÐ²Ð°Ð´Ñ€Ð°Ñ‚Ð¾Ð¼ Ñ ÐºÐ»Ð°ÑÑÐ¾Ð¼ droppable (ÐµÑÐ»Ð¸ ÐµÑÑ‚ÑŒ)
    let droppableBelow = elemBelow.closest('.droppable');

    // Ñ…Ð¾Ñ‡Ñƒ, Ñ‡Ñ‚Ð¾Ð±Ñ‹ Ð² Ð³Ñ€Ð¸Ð´Ðµ Ð½ÐµÐ»ÑŒÐ·Ñ Ð±Ñ‹Ð»Ð¾ Ð¿Ð¾Ð»Ð¾Ð¶Ð¸Ñ‚ÑŒ box Ð½Ð° box:
    if (
      droppableBelow != null &&
      droppableBelow.classList.contains('dropzone__grid-block') &&
      !elemBelow.classList.contains('dropzone__grid-block')
    ) {
      droppableBelow = null;
    }

    if (currentDroppable != droppableBelow) {
      if (currentDroppable) {
        // Ð²Ñ‹Ð»ÐµÑ‚Ð°ÐµÐ¼ Ð¸Ð· droppable
        leaveDroppable(currentDroppable);
      }
      currentDroppable = droppableBelow;
      if (currentDroppable) {
        // Ð²Ð»ÐµÑ‚Ð°ÐµÐ¼ Ð² droppable
        enterDroppable(currentDroppable);
      }
    }
    // Ð¿Ð¾Ð´ÑÐ²ÐµÑ‚ÐºÐ° ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°, Ð² ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ Ð¼Ð¾Ð¶Ð½Ð¾ Ð²ÑÑ‚Ð°Ð²Ð¸Ñ‚ÑŒ ÐºÐ²Ð°Ð´Ñ€Ð°Ñ‚
    function enterDroppable(elem) {
      elem.style.background = dropBacklight;
    }
    // Ð¼ÐµÐ½ÑÐµÐ¼ background Ð¾Ð±Ñ€Ð°Ñ‚Ð½Ð¾
    function leaveDroppable(elem) {
      elem.style.background = dropzoneColor;
    }
  }

  // Ð»Ð¸ÑÑ‚Ð½ÐµÑ€ Ð¿ÐµÑ€ÐµÐ¼ÐµÑ‰ÐµÐ½Ð¸Ñ Ð¿Ð¾ ÑÐºÑ€Ð°Ð½Ñƒ
  document.addEventListener('pointermove', onPointerMove);

  // ÐºÐ»Ð°Ð´ÐµÐ¼ ÐºÐ²Ð°Ð´Ñ€Ð°Ñ‚, ÑƒÐ´Ð°Ð»ÑÐµÐ¼ Ð±Ð¾Ð»ÐµÐµ Ð½ÐµÐ½ÑƒÐ¶Ð½Ñ‹Ðµ Ð¾Ð±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸ÐºÐ¸ ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ð¹
  event.target.onpointerup = function () {
    document.removeEventListener('pointermove', onPointerMove);
    event.target.onpointerup = null;

    if (currentDroppable) {
      currentDroppable.append(event.target);
      //ÑƒÐ±Ð¸Ñ€Ð°ÐµÐ¼ Ð¿Ð¾Ð´ÑÐ²ÐµÑ‚ÐºÑƒ droppable Ð·Ð¾Ð½Ñ‹
      currentDroppable.style.background = dropzoneColor;
      if (currentDroppable.classList.contains('dropzone__grid-block')) {
        //ÐºÐ»Ð°Ð´ÐµÐ¼ Ð² Ð·Ð¾Ð½Ñƒ Ñ ÑÐµÑ‚ÐºÐ¾Ð¹, Ð´ÐµÐ»Ð°ÐµÐ¼ Ð¿Ð¾Ð¿Ñ€Ð°Ð²ÐºÑƒ Ð½Ð° Ð±Ð¾Ñ€Ð´ÐµÑ€
        event.target.style.left = -1 + 'px';
        event.target.style.top = -1 + 'px';
      } else if (currentDroppable.classList.contains('dropzone__nogrid')) {
        //ÐºÐ»Ð°Ð´ÐµÐ¼ Ð² Ð·Ð¾Ð½Ñƒ Ð±ÐµÐ· ÑÐµÑ‚ÐºÐ¸, Ð¼ÐµÐ½ÑÐµÐ¼ Ð¿Ð¾Ð·Ð¸Ñ†Ð¸ÑŽ ÐºÐ²Ð°Ð´Ñ€Ð°Ñ‚Ð° Ð² ÑÐ¾Ð¾Ñ‚Ð²ÐµÑ‚ÑÑ‚Ð²Ð¸ Ñ Ð½Ð¾Ð²Ñ‹Ð¼ Ñ€Ð¾Ð´Ð¸Ñ‚ÐµÐ»ÐµÐ¼
        event.target.style.left =
          parseInt(event.target.style.left) -
          currentDroppable.getBoundingClientRect().left +
          'px';
        event.target.style.top =
          parseInt(event.target.style.top) -
          currentDroppable.getBoundingClientRect().top +
          'px';
      }
    } else {
      //Ð¿Ñ€Ð¾Ð¸Ð·Ð¾ÑˆÐ»Ð¾ Ð¸ÑÑ‡ÐµÐ·Ð½Ð¾Ð²ÐµÐ½Ð¸Ðµ
      event.target.classList.add('DnD__draggable_disapearing');
      setTimeout(() => DnD.removeChild(event.target), 1000);
    }
  };
}
